::-webkit-scrollbar {
    width: 15px;
}

::-webkit-scrollbar-track {
    background: transparent;
}

::-webkit-scrollbar-thumb {
    background: rgb(27, 37, 53);
    border-radius: 1rem;
}





.proj-idea::-webkit-scrollbar {
    width: 0.5rem;
}

.proj-idea::-webkit-scrollbar-thumb {
    background: rgb(0, 160, 253);
    border-radius: 2rem;
}

.project-cards::-webkit-scrollbar {
    display: none;
}